<template>
    <div>
        <van-nav-bar title="今日头条-登录"/>
        <div>
            <van-form @submit="onSubmit">
                <van-field
                    required
                    v-model="user.mobile"
                    name="mobile"
                    label="手机号"
                    placeholder="请输入11位手机号"
                    :rules="[{ required: true, 
                        message: '请填写正确手机号' ,
                        pattern: /^1[3-9]\d{9}$/ 
                        }]"
                />
                <van-field
                    required
                    v-model="user.code"
                    type="password"
                    name="code"
                    label="密码"
                    placeholder="请输入6位密码"
                    :rules="[{ required: true, 
                    message: '请填写正确密码',
                    pattern: /^\d{6}$/ 
                    }]"
                />
                <div style="margin: 16px;">
                    <van-button round block type="info" native-type="submit"
                    :disabled="isLoading"
                    :loading="isLoading"
                    loading-text="正在登录中..."
                    >登录</van-button>
                </div>
            </van-form>
        </div>
    </div>
</template>

<script>
import { loginAPI } from '@/api'
import { Notify } from 'vant'
import { setToken } from '@/utils/token.js'
export default {
    name:'Login',
    data() {
        return {
            user: {
                mobile: '15802713161', // 手机号
                code:'246810', // 验证码（密码-必须是246810）
            },
            isLoading:false // 登录按钮加载状态
        };
    },
    methods: {
        // 需要通过验证才能提交
        async onSubmit(values) { 
            this.isLoading = true
            try {
                const res = await loginAPI(this.user) 
                Notify({ type: 'success', message: '登陆成功' })
                setToken(res.data.data.token)
                localStorage.setItem('refresh_token', res.data.data.refresh_token)
                this.$router.replace('/layout/home')
            } catch (err) {
                Notify ({type:'danger',message:'账号或密码错误'})
            }
            this.isLoading = false 
        },
    },
}
</script>

